$main: #ff6600;
$light: #fff;
$dark: #000;

@mixin center {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin space {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@mixin even {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
@mixin container {
  max-width: 1400px;
  padding: 0 25px;
  margin: 0 auto;
}
@mixin image {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@mixin transition {
  transition: filter 0.45s cubic-bezier(0, 0, 0, 1),
    transform 0.45s cubic-bezier(0, 0, 0, 1);
}

a {
  color: #000;
}

/*************************************************
*                    Header                      *
*************************************************/
.desktop {
  .header {
    position: sticky;
    top: 0;
    left: 0;
    padding: 1.5rem 0;
    z-index: 10;
    background-color: #f5f5f5;
    box-shadow: 0px 3px 10px rgba($dark, 0.1);

    .top {
      @include container;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 10px;

      .search {
        position: relative;
        @include center();
        justify-content: flex-start;
        width: 70%;
        input {
          box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
          padding: 0.5rem;
          width: 100%;
          height: 30px;
        }

        .search-btn {
          position: absolute;
          right: 0;
          top: 50%;
          width: 40px;
          height: 30px;
          transform: translateY(-50%);
          background-color: $main;
          @include center();
          font-size: 12px;
          color: $light;
        }
      }

      .for-logo {
        @include center();
        .logo {
          width: 80%;
        }
      }

      .sign-cart {
        @include center();
        justify-content: flex-end;
        font-size: 15px;

        i {
          font-size: 13px;
          margin-right: 7px;
        }

        .cart {
          margin-left: 2rem;
          cursor: pointer;
          @include center();
        }

        .login {
          margin-left: 2rem;
          cursor: pointer;
          @include center();
        }

        .sign-in {
          cursor: pointer;
          @include center();
        }
      }
    }

    .nav-bar {
      @include container();
      @include center();
      margin-top: 1rem;

      ul {
        @include even();

        li {
          margin: 0 1.5rem;
          line-height: 3rem;
          text-transform: uppercase;
          font-weight: 500;
          border-bottom: 1px solid transparent;
          transition: 0.4s;

          &:hover {
            border-bottom: 1px solid $main;
          }

          &:hover a {
            color: $main;
          }
        }
      }
    }
  }

  /*************************************************
*                    Slider                      *
*************************************************/

  .slider {
    position: relative;
    background-color: $light;

    .container {
      @include container;
      display: grid;
      grid-template-columns: 1fr 1fr;
      height: 400px;
    }

    .wrapper {
      @include center();

      h3 {
        font-weight: 500;
        font-size: 25px;
        position: relative;

        &:before {
          content: '';
          position: absolute;
          top: -17px;
          left: 0;
          width: 42px;
          height: 2px;
          background-color: rgba($dark, 0.5);
        }

        &:after {
          content: '';
          position: absolute;
          top: -7px;
          left: 0;
          width: 84px;
          height: 3px;
          background-color: $dark;
        }
      }

      h6 {
        font-size: 12px;
        font-weight: 400;
        margin: 0.7rem 0;
      }

      .custom-button {
        background-color: $main;
        padding: 1rem 3rem;
        line-height: 4rem;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
        margin-right: 2rem;
      }

      .custom-btn {
        padding: 1rem 3rem;
        line-height: 4rem;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
        position: relative;

        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 30px;
          height: 30px;
          border-left: 1px solid $dark;
          border-top: 1px solid $dark;
        }

        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          right: 0;
          width: 30px;
          height: 30px;
          border-bottom: 1px solid $dark;
          border-right: 1px solid $dark;
        }
      }

      .image-bg {
        height: 200px;
        width: 300px;
        position: relative;

        &:before {
          content: '';
          position: absolute;
          top: -13px;
          left: -13px;
          height: 100%;
          width: 100%;
          background-color: rgba($main, 0.2);
        }

        &:after {
          content: '';
          position: absolute;
          bottom: -13px;
          right: -13px;
          height: 100%;
          width: 100%;
          background-color: rgba($main, 0.6);
        }

        .slider-image {
          position: relative;
          @include image();
          width: 100%;
          height: 100%;
          z-index: 1;
        }
      }
    }
  }

  /*************************************************
*                   directory                    *
*************************************************/

  .directory-menu {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include container();
    margin-top: 1rem;

    .homepage {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 20px 80px;
    }

    .directory-menu {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    .menu-item {
      min-width: 30%;
      height: 300px;
      flex: 1 1 auto;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 7.5px 15px;
      overflow: hidden;

      &:hover {
        cursor: pointer;

        & .background-image {
          transform: scale(1.1);
          transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
        }

        & .content {
          opacity: 0.9;
        }
      }

      &.large {
        height: 380px;
      }

      &:first-child {
        margin-right: 7.5px;
      }

      &:last-child {
        margin-left: 7.5px;
      }

      .background-image {
        width: 100%;
        height: 100%;
        background-position: center;
        background-size: cover;
        overflow: hidden;
      }

      .content {
        height: 90px;
        width: 300px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: rgba($dark, 0.3);
        color: $light;
        backdrop-filter: blur(20px);
        position: absolute;

        .title {
          font-size: 48px;
          font-weight: 300;
          color: $light;
        }

        .subtitle {
          font-weight: lighter;
          font-size: 16px;
        }
      }
    }
  }

  /*************************************************
  *                 special-offers                 *
  *************************************************/

  .special-offers {
    @include container();
    margin-top: 4rem;

    .title {
      text-align: center;
      font-size: 60px;
      margin-bottom: 2rem;
    }
    .special-offers-card-list {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-gap: 20px;

      .special-offers-card {
        .image {
          @include image();
          height: 500px;
          width: 100%;
          position: relative;
          overflow: hidden;

          &:hover {
            .add-to-cart {
              bottom: 0;
              z-index: 1;
            }

            .quick-view {
              height: 100px;
              width: 100px;
              opacity: 1;

              &:hover {
                i {
                  transform: rotate(0);
                  font-size: 30px;
                }
              }
            }
          }

          .ribbon {
            position: absolute;
            right: -35px;
            top: -35px;
            height: 140px;
            width: 140px;
            overflow: hidden;
            .tag {
              position: absolute;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%) rotate(45deg);
              background-color: $main;
              color: $light;
              width: 100%;
              height: 30px;
              @include center();
              text-transform: uppercase;
            }
          }

          .quick-view {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            height: 200px;
            width: 200px;
            border-radius: 50%;
            background-color: rgba($dark, 0.2);
            backdrop-filter: blur(10px);
            @include center();
            font-size: 24px;
            color: $light;
            opacity: 0;
            transition: 0.4s;
            cursor: pointer;

            i {
              transform: rotate(360deg);
              transition: 0.4s;
            }
          }

          .add-to-cart {
            position: absolute;
            bottom: -80px;
            left: 50%;
            transform: translateX(-50%);
            background-color: #141414;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 60px;
            width: 100%;
            color: $light;
            text-transform: uppercase;
            font-size: 20px;
            transition: 0.4s;
            z-index: -1;
            cursor: pointer;
          }
        }

        .content {
          margin: 1rem 0;
          h4 {
            font-size: 24px;
            font-weight: 400;
            margin-bottom: 1rem;
          }

          h6 {
            font-size: 18px;
            font-weight: 400;
            color: $main;
          }
        }
      }
    }
  }

  /*************************************************
  *                 feature-product                *
  *************************************************/

  .feature-product {
    @include container();
    margin-top: 4rem;

    .title {
      text-align: center;
      font-size: 60px;
      margin-bottom: 2rem;
    }
    .card-list {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 20px;

      .card {
        position: relative;
        height: 310px;

        &:hover {
          .for-image {
            .image {
              transform: scale(1.2);
            }

            .explore {
              width: 100%;
              opacity: 1;
            }
          }
        }

        .for-image {
          width: 100%;
          height: 100%;
          overflow: hidden;
          position: relative;

          &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background-image: linear-gradient(
              180deg,
              rgba(255, 255, 255, 0),
              rgba(0, 0, 0, 0.6)
            );
          }

          .explore {
            background-color: #141414;
            color: $light;
            text-transform: uppercase;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            @include center();
            height: 50px;
            width: 1%;
            opacity: 0;
            cursor: pointer;
            z-index: 2;
            @include transition();
            transition: 0.4s;
          }

          .image {
            @include image();
            width: 100%;
            height: 100%;
            @include transition();
          }
        }

        .content {
          position: absolute;
          bottom: 0;
          left: 0;
          padding: 1rem;

          h4 {
            color: $light;
            font-size: 26px;
            font-weight: 400;
          }

          h6 {
            color: $main;
            font-size: 20px;
            font-weight: 400;
          }
        }
      }
    }
  }

  /*************************************************
  *                    follow                      *
  *************************************************/

  .follow {
    position: relative;
    height: 740px;
    width: 100%;
    @include image();
    margin-top: 4rem;
    @include center();
    background-attachment: fixed;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-color: rgba(0, 0, 0, 0.5);
    }

    .content {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 1;
      text-align: center;
      width: 100%;

      h2 {
        text-transform: uppercase;
        color: $light;
        font-size: 60px;
        font-weight: 400;
        margin-bottom: 2rem;
      }

      .social-icon {
        background-color: $light;
        text-align: center;
        padding: 2rem;
        display: inline-block;
        i {
          margin: 0 2rem;
          font-size: 30px;
          cursor: pointer;

          &.fa-instagram {
            color: linear-gradient(
              circle at 30% 107%,
              #fdf497 0%,
              #fdf497 5%,
              #fd5949 45%,
              #d6249f 60%,
              #285aeb 90%
            );
          }
        }
      }
    }
  }

  /*************************************************
  *                    footer                      *
  *************************************************/

  .footer {
    position: relative;
    @include image();

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.8);
    }
    .content-list {
      @include container();
      position: relative;
      z-index: 1;
      color: $light;
      padding: 4rem 0;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;

      .content {
        text-align: left;
        h4 {
          font-size: 50px;
          font-weight: 300;
          margin-bottom: 2rem;
        }

        h6 {
          font-weight: 300;
          font-size: 24px;
          margin-bottom: 1rem;
        }
      }

      .slide-image {
        overflow: hidden;

        .swiper-wrapper {
          transition-duration: 1s !important;
        }
        .slide {
          @include image();
          width: 100%;
          height: 300px;
        }
      }
    }
  }
}
